import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DriverCrudComponent } from './driver-crud/driver-crud.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
import { TABS_DEMO_ROUTES } from './driver-crud/routes';

const routes: Routes = [
  {
    path: 'crud', component: DriverCrudComponent, canActivate: [AuthGuard], children: TABS_DEMO_ROUTES
  },
  {
    path: 'driverRfid',  
    canActivate: [AuthGuard],
    loadChildren: () => import('app/main/driver/driver-rfid/driver-rfid.module').then(m => m.DriverRfidModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DriverRoutingModule { }
