import { Component, OnInit, ViewChild } from '@angular/core';
import * as XLSX from 'xlsx';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { MatTableFilter } from 'mat-table-filter';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatSort } from '@angular/material/sort';
import { Observable } from 'rxjs';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { CpoService } from 'app/shared/services/cpo/cpo.service';
import { map, startWith } from 'rxjs/operators';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import { RfidService } from 'app/shared/services/rfid/rfid.service';
import { DriverService } from 'app/shared/services/driver/driver.service';
import { MessageConstant } from 'app/shared/utils/message.constant';
export class Driver {
  id: string;
  cpo_id: number;
  cpo_name: string;
  name: string;
  address1: string;
  address2: string;
  PIN: number;
  landmark: string;
  city_id: number;
  state_id: number;
  country_id: number;
  mobile: string;
  status: string;
  created_date: string;
  created_by: number; // corrected property name
  statusString: string;
  modify_date: string;
  modify_by: number; // corrected property name
  action: string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

  filterEntity: Driver;
  filterType: MatTableFilter;
  head = [['CPO Name', 'Driver Name', 'Mobile', 'Status']];
  public dataSource = new MatTableDataSource<any>([]);
  localStorage: any;
  form: FormGroup;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  cposName: any;
  clientName: any;
  filteredOptionsClient: Observable<any[]>;
  filteredOptionsCPO: Observable<any[]>;
  rfid: Driver[];
  localCpoid: any;
  columns: any[];
  displayedColumns: string[];
  groupByColumns: string[] = [];
  driverData: any[];
  _allGroup: any[];

  expandedRfid: any[] = [];
  expandedSubRfid: Driver[] = [];
  Row: any;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  clientSearch = false;
  cpoSearch = false;
  nameSearch = false;
  mobileSearch = false;
  statusSearch = false;
  exportedData: any[];
  cpoRfid: any;
  selectedCpo_id: any = 0;

  constructor(
    private driverService: DriverService,
    public dialog: MatDialog,
    private snackBar: MatSnackBar,
    private fb: FormBuilder,
    private cpoService: CpoService,
    private rfidService: RfidService
  ) {
    // Create 100 users
    // const users = Array.from({length: 100}, (_, k) => createNewUser(k + 1));

    // Assign the data to the data source for the table to render
    this.form = this.fb.group({
      CPO: ['']
    });
  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.columns = [
      {
        field: 'cpo_name'
      },
      {
        field: 'name'
      },
      {
        field: 'mobile'
      },
      {
        field: 'status'
      },
      {
        field: 'action'
      }
    ];
    this.displayedColumns = this.columns.map(column => column.field);

    this.getactivecposcw(this.localStorage.userId)
    this.filterEntity = new Driver();
    this.filterType = MatTableFilter.ANYWHERE;
    // this.getDriverData(this.selectedCpo_id);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getDriverData(cpo_id?: number) {
    
    this.driverService.getDriverById(this.localStorage.userId, cpo_id).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.driverData = resp.data;
          for (var i = 0; i < this.driverData.length; i++) {
            if (this.driverData[i].status == 'N') {
              this.driverData[i].statusString = 'Inactive';
            } else {
              this.driverData[i].statusString = 'Active';
            }
          }
          this.dataSource = new MatTableDataSource(this.driverData);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this.driverData = [];
          this.dataSource = new MatTableDataSource(this.driverData);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
          return;
        }
      }, (err) => { }
    );
  }

  getactivecposcw(id) {
    this.cpoService.getactivecposcwwithAll(id).subscribe(
      (data: any) => {
        //;
        this.cposName = data;
        if (this.localStorage.role_code != "END") {
          if (this.cposName != null && this.cposName != undefined) {
            // const toSelect = this.cposName.length == 1 ? this.cposName[0] : this.cposName[1];
            const toSelect = this.cposName[0];

            this.selectedCpo_id = toSelect.id;
            this.form.get('CPO').setValue(toSelect);

            if (this.localStorage.role_code == "CPO") {
              this.form.get('CPO').disable();
            }

            this.getDriverData(this.selectedCpo_id);
          }
          this.filteredOptionsCPO = this.form.controls.CPO.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCPO(name) : this.cposName)
          );
        }
      }, (err) => { }
    )
  }

  displayCPO(item: any): string {
    return item && item.name ? item.name : '';
  }

  displayClient(item: any): string {
    return item && item.name ? item.name : '';
  }

  private _filterCPO(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cposName.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  delete(element) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });

    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.driverService.delete(element.id).subscribe(
          (data: any) => {
            this.getDriverData(this.selectedCpo_id);
            this.snackBar.open('Deleted successfully', 'Dismiss', { duration: 2000 });
          }, (err) => {

          }
        )
      }
      this.confirmDialogRef = null;
    });
  }

  onCPOChange(cpo_id) {
    this.selectedCpo_id = cpo_id;
    this.getDriverData(cpo_id);
  }

  openDialog(action, data): void {

    let params = {
      action: action,
      data: data,
      rfidData: this.rfid
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      this.getDriverData(this.selectedCpo_id);
    });
  }

  viewDialog(action, data): void {
    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {

      // this.moderate(result);
      this.getDriverData(this.selectedCpo_id);
    });
  }

  changeVisibility(visibleOption, ev) {
    ev.stopPropagation();

    if (visibleOption == 'cpoSearch') {
      this.cpoSearch = !this.cpoSearch;
      this.filterEntity.cpo_name = "";
    }

    if (visibleOption == 'nameSearch') {
      this.nameSearch = !this.nameSearch;
      this.filterEntity.name = "";
    }

    if (visibleOption == 'mobileSearch') {
      this.mobileSearch = !this.mobileSearch;
      this.filterEntity.mobile = '';
    }

    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = '';
    }
  }

  tableExport() {

    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var driverData = this.dataSource.filteredData[i];
      var data = {
        'CPO Name': driverData.cpo_name,
        'Name': driverData.name,
        'Mobile': driverData.mobile,
        'Status': driverData.statusString
      };
      this.exportedData.push(data);
    }


    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();

    XLSX.utils.book_append_sheet(workBook, workSheet, 'Driver List');
    XLSX.writeFile(workBook, 'Driver List.xlsx');
  }

  pdfExport() {
    //this.preloader.open();
    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var driverData = this.dataSource.filteredData[i];
      var data = [driverData.cpo_name, driverData.name, driverData.mobile, driverData.statusString];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Driver List Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')
    //this.preloader.close();
    // below line for Download PDF document  
    doc.save('Driver List report.pdf');

  }

  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example

    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right")
    return doc;
  }
}
